.root {
  position: relative;
  display: inline-block;
  font-size: 0;

  .svg {
    max-width: 100%;
  }

  .circle,
  .circleBg {
    fill: none;
    transform: rotate(-90deg);
    transform-origin: center;
  }

  .text {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 10px;
  }

  .textProgress {
    margin: 0;
    font-weight: 500;
    font-size: 21px;
    line-height: 26px;
    color: #2c343c;
  }

  .textDescription {
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #2c343c;
  }
}
